  margin-right: 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 1.5rem;
  transform: translateY(-10px);
}
.popular-header:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 2;
  border-radius: 8px;
}

.popular-header > * {
  position: relative;
  z-index: 3;
}

@media (max-width: 800px) {
  .popular-header {
    flex: 0 0 175px;
  }
}

